  .hero-container {
    background: url('chessMonkeys.png') center center/cover no-repeat;
    /* background-size: 75%; */
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
  }
  
  .hero-container > h1 {
    color: #4d4d4d;
    font-size: 100px;
    margin-top: -100px;
    text-align: center;
  }
  
  .hero-container > h3 {
    color: #fff;
    font-size: 24px;
    text-align: center;
  }
  
  .hero-container > p {
    margin-top: 8px;
    color: #fff;
    font-size: 32px;
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
      "Lucida Sans", Arial, sans-serif;
  }
  
  .hero-text-container {
    background-color: rgba(24, 26, 27, 0.8);
    color: #fff;
    padding: 20px;
    border-radius: 10px;
  }
  
  .lighter-dark {
    background-color: #333;
  }
  
  .hero-btns {
    margin-top: 32px;
  }
  
  .hero-btns .btn {
    margin: 6px;
  }
  
  .fa-play-circle {
    margin-left: 4px;
  }
  
  @media screen and (max-width: 960px) {
    .hero-container > h1 {
      font-size: 70px;
      margin-top: -150px;
    }
  }
  
  @media screen and (max-width: 768px) {
    
    .hero-container > h1 {
      font-size: 36px;
      margin-top: 150px;
    }
  
    .hero-container > h3 {
      font-size: 18px;
    }
  
    .hero-container > p {
      font-size: 30px;
    }
  
    .btn-mobile {
      display: block;
      text-decoration: none;
    }
  
    .btn {
      width: 100%;
    }
  
    .hero-container {
      height: 50vh;
    }
  }